import React from 'react'
import { useHistory } from 'react-router-dom'
import { Button, Col, Row, Image, Typography, Space } from 'antd'

import ServiciosService from './../service/ServiciosService'
const serviciosService = new ServiciosService()

const Servicios = () => {
    const history = useHistory()
    const servicios = serviciosService.gets()
    const go = (url) => {
        history.push(`/${url}`);
    }
    const renderServicio = ({ id, titulo, nombre, imagen, url }) => <Col key={id} xs={24} sm={12} md={12} lg={12} xl={8} xxl={8} style={{ textAlign: 'center' }}>
        <Typography.Title level={4} style={{ color: '#ffffff', letterSpacing: '1px' }}>
            <blockquote>
                {titulo}
            </blockquote>
        </Typography.Title>
        <Image alt={nombre} className='bdRoundBorder' src={`/assets/images/planAccion/servicios/${imagen}`} onClick={() => go(url)} preview={false} />
    </Col>

    if (servicios && servicios.id) {
        const { codigo, titulo, subTitulo, imagenTitulo, botones, items } = servicios
        return (
            <>
                <Row justify='center' gutter={[20, 20]}>
                    <Col xs={22} sm={20} md={16} lg={16} xl={16} xxl={16}>
                        <Row justify='center'>
                            <Col xs={24} sm={24} md={24} lg={22} xl={22} xxl={16}>
                                {
                                    imagenTitulo ?
                                        <Image alt={titulo} src={`/assets/images/planAccion/${codigo}/${imagenTitulo}`} preview={false} />
                                        :
                                        <>
                                            <Typography.Title level={1}>
                                                {titulo}
                                            </Typography.Title>
                                            <Typography.Title level={2}>
                                                {subTitulo}
                                            </Typography.Title>
                                        </>
                                }
                            </Col>
                            {
                                items && items.length > 0 &&
                                <Col span={24}>
                                    <Row gutter={[15, 15]} justify='center'>
                                        {
                                            items.map(s => renderServicio(s))
                                        }
                                    </Row>
                                </Col>
                            }
                        </Row>
                    </Col>
                </Row>
                <Row>
                    <Col xs={24} sm={24} md={24} lg={24} xl={24} xxl={24} style={{ textAlign: 'center', paddingTop: '20px' }}>
                        <div className='bdNav'>
                            <ul>
                                {
                                    botones.map(({ id, titulo, url }) => <li key={id} onClick={() => go(url)}>
                                        {titulo}
                                        <span></span><span></span><span></span><span></span>
                                    </li>)
                                }
                            </ul>
                        </div>
                        {/* <Space>
                            {
                                botones.map(b =>
                                    <Button key={b.id} type={b.type} icon={b.icon} onClick={() => go(b.url)}>{b.titulo}</Button>
                                )
                            }
                        </Space> */}
                    </Col>
                </Row >
            </>
        )
    }
}

export default Servicios